import React, { useEffect, useState, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import Card from "../component/Card";

const INITIAL_PAGE = 1;

const Explorepages = () => {
    const { explore } = useParams();
    const [pageNo, setPageNo] = useState(INITIAL_PAGE);
    const [data, setData] = useState([]);
    const [totalPageNo, setTotalPageNo] = useState(0);
    const [loading, setLoading] = useState(false); // State for loading status

    const fetchData = useCallback(async () => {
        if (pageNo > totalPageNo && totalPageNo !== 0) return;

        setLoading(true);
        try {
            const response = await axios.get(`/discover/${explore}`, {
                params: { page: pageNo }
            });
            setData((prev) => [...prev, ...response.data.results]);
            setTotalPageNo(response.data.total_pages);
        } catch (error) {
            console.error("Error fetching data:", error);
        } finally {
            setLoading(false); // Reset loading state
        }
    }, [pageNo, explore, totalPageNo]);

    const handleScroll = useCallback(() => {
        if (window.innerHeight + window.scrollY >= document.body.offsetHeight) {
            setPageNo((prev) => prev + 1);
        }
    }, []);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    useEffect(() => {
        setPageNo(INITIAL_PAGE);
        setData([]);
        fetchData();
    }, [explore, fetchData]);

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [handleScroll]);

    return (
        <div className='py-16'>
            <div className='container mx-auto'>
                <h3 className='capitalize text-lg lg:text-xl font-semibold my-2'>
                    Popular {explore} Shows
                </h3>
                <div className='grid grid-cols-[repeat(auto-fit,230px)] gap-6 justify-center lg:justify-start'>
                    {data.map((exploreData) => {
                        const cardKey = `${exploreData.id}-exploreSection`;
                        return (
                            <Card
                                key={cardKey}
                                data={exploreData}
                                media_type={explore}
                            />
                        );
                    })}
                </div>
                {loading && <p>Loading...</p>} {/* Loading indication */}
            </div>
        </div>
    );
};

export default Explorepages;
